.footer__social {
  display: block;
  width: 47px;
  height: 48px;
  background-color: #81b3d2;
  overflow: hidden;
}

.footer__icon-active {
  display: none;
}

.footer__social:hover {
  background-color: #669ec0;
}

.footer__social:active {
  background-color: #5496bd;

  .footer__icon {
    display: none;
  }

  .footer__icon-active {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}

.footer {
  overflow: hidden;
}

.footer__block {
  width: $Screen-size*1px;
  max-width: 1200px;
  background-color: white;
  padding-top: 37px;
  margin-left: auto;
  margin-right: auto;
}

.footer__text {
  text-align: center;
  margin-top:15px;
}

.footer__social--1 {
  margin-left: auto;
}

.footer__social--3 {
  margin-right: auto;
}

.footer__social--2 {
  margin-left: 6px;
  margin-right: 6px;
}

.footer__icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.footer__icon-active--1 {
  @extend .footer__icon--1;
}

.footer__icon-active--2 {
  @extend .footer__icon--2;
}

.footer__icon-active--3 {
  @extend .footer__icon--3;
}

.footer__icon--1 {
  display: block;
  margin-top: 17px;
  width: 18px;
  height: 14px;
}

.footer__icon--2 {
  width: 9px;
  height: 18px;
  margin-top: 14px;
}

.footer__icon--3 {
  width: 20px;
  height: 14px;
}

.footer__social:active .footer__icon--1 {
  background: url("../../img") 0px 0px;
  background-size: 100% 100%;
}

.footer__icon--1,
.footer__icon--3 {
  margin-top: 17px;
}

.footer__text {
  margin-bottom: 42px;
  line-height: 17px;
}

.footer__copyright {
  margin-top: 49px;
  margin-left: 76px;
}

.footer__icon--4,
.footer__icon--5,
.footer__icon--6 {
  width: 27px;
  height: 34px;
  margin-top: 42px;
  margin-left: 11px;
  background-size: 100% 100%;
}

.footer__icon--5,
.footer__icon--6 {
  display: none;
}

.footer__copyright {
  @include font(PTSans, 14px, 400, black, left);
}

.footer__link:hover {
  .footer__icon--4 {
    display: none;
  }

  .footer__icon--5 {
    display: block;
  }
}

.footer__link:active {
  .footer__icon--4 {
    display: none;
  }

  .footer__icon--5 {
    display: none;
  }

  .footer__icon--6 {
    display: block;
  }
}
